import { useState } from 'react'
import Basic from './Basic'

const Routers = ({ RoutersList }) => {
  let key = 1
  let ID = 1

  const [hostname, setHostname] = useState('RR')
  const [banner, setBanner] = useState('#Roberth Romero, SCM#')
  const [ospfID, setOspfID] = useState(115)

  return (
    <>
      <div
        style={{
          padding: '2%',
          textAlign: 'center'
        }}
      >
        <form>
          <label htmlFor='hostname'>Hostname</label>
          <input
            className='mr-3 ml-1'
            type='text'
            autocomplete="off"
            id='hostname'
            onChange={(e) => setHostname(e.target.value)}
          />
          <label htmlFor='banner'>Banner</label>
          <input
            className='mr-3 ml-1'
            type='text'
            id='banner'
            autocomplete="off"
            onChange={(e) => setBanner(e.target.value)}
          />
          <label className='label' htmlFor='OSPF-ID'>
            Ospf ID
          </label>
          <input
            type='number'
            className='ml-3 mr-3 input-number'
            min='1'
            max='255'
            autocomplete="off"
            required
            placeholder={ospfID ? ospfID : 115}
            onChange={(e) => setOspfID(e.target.value)}
          />
        </form>
      </div>

      <div style={{ display: "flex", flexWrap: 'wrap'}}>
        {RoutersList.map((item) =>
          item ? (
            <Basic
              list={item}
              key={key++}
              ID={ID++}
              hostname={hostname}
              banner={banner}
              ospfID={ospfID}
            />
          ) : (
            true
          )
        )}
      </div>
    </>
  )
}

export default Routers
